@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;1,500;2,600&display=swap);
body{margin:0;font-family:'Raleway', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;width:100vw;height:100vh;overflow:hidden}*{box-sizing:border-box}#root{width:100%;height:100%}

.loader-component .loader-circle-container{width:100%;height:100%;position:relative}.loader-component .loader-circle-container .loader-inner-circle{transition:100ms;transform:rotate(90deg);transform-origin:50% 50%}.loader-component .loader-circle-container .loader-progress{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}

.home-container{width:100%;height:100%;display:grid;justify-content:center;align-items:center}.home-container .content-container{display:grid;grid-auto-flow:row;justify-items:center;grid-gap:30px;gap:30px}.home-container .content-container .buttons{display:grid;grid-gap:30px;gap:30px;grid-auto-flow:column}.home-container .content-container .buttons .btn{background-color:#FFFFFF;border:1px solid black;border-radius:3px;padding:8px 15px;min-width:90px;text-align:center;font-weight:400;cursor:pointer;transition:250ms;-webkit-user-select:none;-ms-user-select:none;user-select:none}.home-container .content-container .buttons .btn:hover{border-color:#02C39A;background-color:#ddfff8;font-weight:500;box-shadow:0px 0px 5px 0px rgba(0,0,0,0.3)}

